'use client'

import { createContext, useContext, useEffect, useState, ReactNode } from 'react'
import { supabase } from '@/lib/auth'

interface PaymentSettings {
  commission_rate: number
  payment_methods: string[]
  mobile_money_providers: string[]
  bank_details: {
    bank_name: string
    account_name: string
    account_number: string
    branch_code: string
  }
  mobile_money_details: {
    mtn: {
      provider_name: string
      phone_number: string
      account_name: string
      merchant_id: string
      payment_instructions: string
    }
    airtel: {
      provider_name: string
      phone_number: string
      account_name: string
      merchant_id: string
      payment_instructions: string
    }
  }
}

interface PaymentContextType {
  paymentSettings: PaymentSettings | null
  loading: boolean
  error: string | null
  refreshSettings: () => Promise<void>
}

const defaultPaymentSettings: PaymentSettings = {
  commission_rate: 5,
  payment_methods: ['mobile_money', 'bank_transfer', 'cash'],
  mobile_money_providers: ['mtn', 'airtel'],
  bank_details: {
    bank_name: '',
    account_name: '',
    account_number: '',
    branch_code: ''
  },
  mobile_money_details: {
    mtn: {
      provider_name: 'MTN Mobile Money',
      phone_number: '',
      account_name: '',
      merchant_id: '',
      payment_instructions: ''
    },
    airtel: {
      provider_name: 'Airtel Money',
      phone_number: '',
      account_name: '',
      merchant_id: '',
      payment_instructions: ''
    }
  }
}

const PaymentContext = createContext<PaymentContextType | undefined>(undefined)

export function PaymentProvider({ children }: { children: ReactNode }) {
  const [paymentSettings, setPaymentSettings] = useState<PaymentSettings | null>(null)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)

  const fetchPaymentSettings = async () => {
    try {
      setLoading(true)
      setError(null)

      const { data, error: fetchError } = await supabase
        .from('settings')
        .select('*')
        .single()

      if (fetchError && fetchError.code !== 'PGRST116') {
        throw fetchError
      }

      if (data) {
        setPaymentSettings({
          commission_rate: data.commission_rate || defaultPaymentSettings.commission_rate,
          payment_methods: data.payment_methods || defaultPaymentSettings.payment_methods,
          mobile_money_providers: data.mobile_money_providers || defaultPaymentSettings.mobile_money_providers,
          bank_details: data.bank_details || defaultPaymentSettings.bank_details,
          mobile_money_details: data.mobile_money_details || defaultPaymentSettings.mobile_money_details
        })
      } else {
        // Use default settings if none exist
        setPaymentSettings(defaultPaymentSettings)
      }
    } catch (err) {
      console.error('Error fetching payment settings:', err)
      setError('Failed to load payment settings')
      setPaymentSettings(defaultPaymentSettings)
    } finally {
      setLoading(false)
    }
  }

  const refreshSettings = async () => {
    await fetchPaymentSettings()
  }

  useEffect(() => {
    fetchPaymentSettings()
  }, [])

  return (
    <PaymentContext.Provider value={{
      paymentSettings,
      loading,
      error,
      refreshSettings
    }}>
      {children}
    </PaymentContext.Provider>
  )
}

export function usePayment() {
  const context = useContext(PaymentContext)
  if (context === undefined) {
    throw new Error('usePayment must be used within a PaymentProvider')
  }
  return context
}

// Helper functions for common payment operations
export function usePaymentMethods() {
  const { paymentSettings } = usePayment()
  
  return {
    getAvailableMethods: () => paymentSettings?.payment_methods || [],
    getMobileMoneyProviders: () => paymentSettings?.mobile_money_providers || [],
    getBankDetails: () => paymentSettings?.bank_details,
    getMobileMoneyDetails: (provider: 'mtn' | 'airtel') => paymentSettings?.mobile_money_details?.[provider],
    getCommissionRate: () => paymentSettings?.commission_rate || 5,
    isMethodAvailable: (method: string) => paymentSettings?.payment_methods?.includes(method) || false,
    isProviderAvailable: (provider: string) => paymentSettings?.mobile_money_providers?.includes(provider) || false,
    getPlatformInfo: () => ({
      name: paymentSettings?.platform_name || 'IkazeProperty',
      email: paymentSettings?.platform_email || 'support@ikazeproperty.rw',
      phone: paymentSettings?.platform_phone || '+250 788 123 456',
      address: paymentSettings?.platform_address || 'Kigali, Rwanda'
    })
  }
}
